<template>
  <span>
    <input
            type="radio"
            :value="label"
            :name="name"
    >
    <span
            class="pc-unselect pc-form-radio"
            :class="{
        'pc-form-radioed': value === label,
        'pc-radio-disbaled pc-disabled': disabled
      }"
            @click="handleClick"
    >
      <i
              v-if="value !== label"
              class="pc-anim ccyicon pc-anim-scaleSpring"
              :class="{ 'pc-form-radioed': value !== label }"
      >&#xe7c9;</i>
      <i
              v-if="value === label"
              class="pc-anim ccyicon pc-anim-scaleSpring"
              :class="{ 'pc-form-radioed': value === label }"
      >&#xe631;</i>
      <span><slot/></span>
    </span>
  </span>
</template>

<script>
    export default {
        name: 'radio',
        props: {
            value: {
                type: [String, Number],
                default: ''
            },
            label: {
                type: [String, Number],
                default: ''
            },
            disabled: Boolean,
            name: {
                type: String,
                default: ''
            }
        },
        inject: ['formItem'],
        methods: {
            handleClick() {
                if (!this.disabled) {
                    this.$emit('change', this.label);
                    this.$emit('input', this.label);
                    this.formItem && this.formItem.validate('change');
                }
            }
        }
    };
</script>

<style>
    .pc-form input[type=radio] {
        display: none;
    }

    .pc-form-radio {
        line-height: 28px;
        margin: 6px 10px 0 0;
        padding-right: 10px;
        cursor: pointer;
        font-size: 0;
    }

    .pc-form-radio, .pc-form-radio * {
        display: inline-block;
        vertical-align: middle;
    }

    .pc-form-radio > i:hover, .pc-form-radioed > i {
        color: #FF6100;
    }

    .pc-form-radio * {
        font-size: 14px;
    }

    .pc-form-radio > i:hover, .pc-form-radioed > i {
        color: #FF6100;
    }

    .pc-form-radio > i {
        margin-right: 8px;
        font-size: 22px;
        color: #c2c2c2;
    }

    .pc-form-radio > i:hover, .pc-form-radioed > i {
        color: #FF6100;
    }

    .pc-radio-disbaled > i {
        color: #e2e2e2 !important;
    }
</style>
